<div class="mx-auto grid gap-2 grid-cols-2 place-items-center border-red-900" style="max-width: 100rem; height: 50vh" in:fade="{{duration: 500}}">
  <div class="bannerOpacity border-blue-900 flex flex-col justify-center items-center" style='background-image: url("homepage.png"); background-size: 50%; background-repeat: no-repeat; background-position: center;'>
    <img src='homepage3.png' id='homepage' class='homepage' style='z-index: -1; transform: rotate(0deg);' alt='logo' width='120%' />
  </div>
  <div class="bannerOpacity border-blue-900 h-full w-full relative flex  flex-col justify-center items-center">
      <div class="flex flex-inline">
