      <div class='flex flex-inline mt-10 justify-center'>    
        <Countdown from="2022-12-15 19:00:00" dateFormat="YYYY-MM-DD H:m:s" zone="Europe/Paris" let:remaining>
          <div class="whatever">
              <h4 class="lg:text-3xl font-bold text-white pt-8 md:text-6xl text-4xl">
