{#if featureFlag}
  <div class="bannerOpacity flex items-start justify-center mt-44 text-white">
    <IndexCounter indexCounterCaption='Marketcap'  indexCounterMaxNumber={$priceData['marketCap']}        indexCounterUnit='format' svgName='Marketcap' />
    <IndexCounter indexCounterCaption='TVL'        indexCounterMaxNumber={$priceData['totalValueLocked']} indexCounterUnit='format' svgName='Tvl' />
    <IndexCounter indexCounterCaption='SINU Price' indexCounterMaxNumber={$priceData['sinuPrice']}        indexCounterUnit='dollar' svgName='Dollar' />
  </div>
{/if}


<script>
    import Link from './../components/link.svelte';
    import IndexCounter from './../components/indexCounter.svelte';
    import {fade, blur, fly, slide, scale, draw, crossfade} from 'svelte/transition'
    import { onMount } from 'svelte';
    import { priceData } from './../stores.js';
    import Countdown from 'svelte-countdown/src/index.js';

    $: featureFlag = false;

    onMount(async () => {
      var bannerItems = document.getElementsByClassName('bannerOpacity');
      Array.prototype.forEach.call(bannerItems, function(slide, index) {
          bannerItems[index].style.opacity = 1;
      });


      var startTime = new Date();

      document.addEventListener('mousemove', (e) => {
        if(document.getElementById('homepage')) {
          var width = Math.abs((window.innerWidth / 2 - e.pageX) / 10)  / 30;
          var height = Math.abs((window.innerHeight / 2 - e.pageY) / 5) / 30;

          var currentPosition = parseFloat(document.getElementById('homepage').style.transform.split('rotate(')[1].split('deg')[0])
          var newPosition = currentPosition + height + width +1;

          var currentTime = new Date();
          var timeElapsed = (currentTime - startTime)/1000
          if(Math.round(timeElapsed) > 1) {
            Array.apply(null, {length: (newPosition-currentPosition)}).map(Function.call, Number).map(function (idx) { return idx+currentPosition }).forEach(function(i) {
              if (i == 5000) {
                alert("Well Done ! EasterEgg Found ! Send 'issou' to the Telegram Bot to get a second airdrop!");
              }
              document.getElementById('homepage').style.transform = "rotate("+(i+1)+"deg)";
            })
          }          
        }

      });
    });

</script>