<script>
  import {location} from 'svelte-spa-router'
  import { connected } from './../stores.js';

  $: visibilityClass = 'hide';
  $: ($location || $connected) && triggerVisibility();

  async function triggerVisibility() {
    if($location == '/Farm') {
      visibilityClass = 'hide';
    }
    else if ($location == '/' || $location == '/Index') {
      let sleep = ms => new Promise(resolve => setTimeout(resolve, ms));
      await sleep(2000);
      visibilityClass = 'showDelayed';
    }
    else {
      visibilityClass = 'show';
    }
  }
</script>
